<template>
  <BaseLayout>
    <Breadcrumbs />
    <section class="static pt-7">
      <BContainer>
        <BRow>
          <BCol lg="12" v-if="cmsPageContent != null">
            <h1>{{ cmsPageContent.title }}</h1>
            <div
              v-if="cmsPageContent.content"
              class="cms-content"
              v-html="cmsPageContent.content"
            ></div>
          </BCol>
        </BRow>
      </BContainer>
    </section>
  </BaseLayout>
</template>

<script setup>
import { computed, onMounted, watch } from "vue";
import { useStore } from "vuex";
import { useHead } from "@unhead/vue";
import { useRoute } from "vue-router";
import BaseLayout from "@/base/core/components/common/BaseLayout";
import Breadcrumbs from "@/base/core/components/Breadcrumbs";

const store = useStore();
const route = useRoute();

const cmsPageContent = computed(() => store.getters["blog/getBlog"]);
const identifier = computed(() => route.params.code);

onMounted(async () => {
  store.dispatch("blog/loadBlog", {
    identifier: route.params.code,
  });

  const bcrumb = {
    current: cmsPageContent.value.title,
    routes: [
      {
        name: "Blog",
        route_link: "/blog",
      },
    ],
  };
  store.commit("breadcrumbs/set", bcrumb);
});

watch(identifier, async () => {
  await store.dispatch("blog/loadBlog", {
    identifier: route.params.code,
  });
});

// Set up meta information using useHead
useHead({
  title: () => cmsPageContent.value.title,
  meta: [
    { name: "title", content: () => cmsPageContent.value.meta_title },
    { name: "keywords", content: () => cmsPageContent.value.meta_keywords },
    {
      name: "description",
      content: () => cmsPageContent.value.meta_description,
    },
  ],
});
</script>
